import Vue from 'vue'
import Router from 'vue-router'

// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer')

// Views
const Dashboard = () => import('@/views/Dashboard')

const Members = () => import('@/views/members/members.vue')
const Member = () => import('@/views/members/member.vue')
const Hospitals = () => import('@/views/hospitals/hospitals.vue')
const Hospital = () => import('@/views/hospitals/hospital.vue')
const UserGroups = () => import('@/views/userGroups/userGroups.vue')
const Communication = () => import('@/views/communication/communication.vue')
const Library = () => import('@/views/library/library.vue')
const Forum = () => import('@/views/forum/forum.vue')
const Loans = () => import('@/views/loans/loans.vue')
const RequestLoan = () => import('@/views/loans/requestloan.vue')
const ProcessLoan = () => import('@/views/loans/processloan.vue')
const Equipments = () => import('@/views/equipments/equipments.vue')

Vue.use(Router)

export default new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: DefaultContainer,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        },
        
        {
          path: 'member',
          redirect: '/member/list',
          name: 'Members',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'list',
              component: Members
            },
            {
              path: 'new',
              name: 'New',
              component: Member
            },
            {
              path: 'edit/:id',
              name: 'Edit',
              component: Member
            }
          ]
        },

        {
          path: 'hospital',
          redirect: '/hospital/list',
          name: 'Hospitals',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'list',
              component: Hospitals
            },
            {
              path: 'new',
              name: 'New',
              component: Hospital
            },
            {
              path: 'edit/:id',
              name: 'Edit',
              component: Hospital
            }
          ]
        },
        {
          path: 'userGroups',
          name: 'User Groups',
          component: UserGroups
        },
        {
          path: 'communication',
          name: 'Communication',
          component: Communication
        },
        {
          path: 'library',
          name: 'Library',
          component: Library
        },
        {
          path: 'forum',
          name: 'Forum',
          component: Forum
        },
        {
          path: 'loan',
          redirect: '/loan/list',
          name: 'Loans',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'list',
              component: Loans
            },
            {
              path: 'new',
              name: 'New',
              component: RequestLoan
            },
            {
              path: 'edit/:id',
              name: 'Edit',
              component: RequestLoan
            },
            {
              path: 'process',
              name: "ProcessLoan",
              component: ProcessLoan
            }
          ]
        },
        {
          path: 'equipments',
          name: 'Equipments',
          component: Equipments
        }
      ]
    },
  ]
})
